<template>
  <div class="training">
    <myHeader :isBack="true" title="视频详情"></myHeader>
    <div class="seat-head"></div>
    <div class="container">
      <iframe :src="imgUrl" frameborder="0" class="frameStyle"></iframe>
    </div>
  </div>
</template>

<script>
  import myHeader from '@/components/Header'
  import {getParam} from "@/utils/wxUtil";

  export default {
    name: 'viedoDetail',
    data () {
      return {
        imgUrl:''
      }
    },
    components: {
      myHeader
    },
    created () {
      this._setUrl()
    },
    methods: {
      _setUrl(){
        let id = getParam('rcordId');
        console.log(typeof (id));
        switch (id) {
          case '2':
          this.imgUrl = 'https://v.youku.com/v_show/id_XNDE3NDQ5MTc4OA==.html?spm=a2h3j.8428770.3416059.1';
          break;
          case '3':
            this.imgUrl = 'https://v.youku.com/v_show/id_XNDE3Mzc1NDc0OA==.html?spm=a2h3j.8428770.3416059.1';
            break;
          case '4':
            this.imgUrl = 'https://v.youku.com/v_show/id_XNDE3Mzc4Mzg0MA==.html?spm=a2h3j.8428770.3416059.1';
            break;
          case '5':
            this.imgUrl = 'https://v.youku.com/v_show/id_XNDE3NDE4MzQ2OA==.html?spm=a2h3j.8428770.3416059.1';
            break;
          case '6':
            this.imgUrl = 'https://v.youku.com/v_show/id_XNDE3NDQ5Mzg4MA==.html?spm=a2h3j.8428770.3416059.1';
            break;
          case '7':
            this.imgUrl = 'https://v.youku.com/v_show/id_XNDE3NDQ5NDg0MA==.html?spm=a2h3j.8428770.3416059.1';
            break;
          case '8':
            this.imgUrl = 'https://v.youku.com/v_show/id_XNDE3NDQ5NjcwMA==.html?spm=a2h3j.8428770.3416059.1';
            break;
          case '9':
            this.imgUrl = 'https://v.youku.com/v_show/id_XNDE3NDUwMDE5Ng==.html?spm=a2h3j.8428770.3416059.1';
            break;
          case '10':
            this.imgUrl = 'https://v.youku.com/v_show/id_XNDE3NDQ5ODA1Ng==.html?spm=a2h3j.8428770.3416059.1';
            break;
          case '11':
            this.imgUrl = 'https://v.youku.com/v_show/id_XNDE3NDUwMTY3Ng==.html?spm=a2h3j.8428770.3416059.1';
            break;
          case '12':
            this.imgUrl = 'https://v.youku.com/v_show/id_XNDE3NDUwMzU1Ng==.html?spm=a2h3j.8428770.3416059.1';
            break;
          case '13':
            this.imgUrl = 'https://v.youku.com/v_show/id_XNDE3NDUwNTY4OA==.html?spm=a2h3j.8428770.3416059.1';
            break;
          }
      }
    }
  }
</script>

<style scoped lang="scss">
  #bofang{
    margin-right: 10px;
  }
  .training {
    min-height: 100vh;
    background-color: #F8F8F8;
    position: relative;
  }

  .frameStyle {
    width: 100%;
    height: calc(100% - .15rem);
    top: .45rem;
    left: 0;
    position: absolute;
  }
</style>



